import {
	CHANGE_SEARCH_FIELD,
	REQUEST_ROBOTS_PENDING,
	REQUEST_ROBOTS_SUCCESS,
	REQUEST_ROBOTS_FAILED,
} from './constants'

export const setSearchField = (text) => ({
	type: CHANGE_SEARCH_FIELD,
	payload: text,
})

/* 	
	higher order function, returns function
	by default redux does not understand this
	redux thunk is listening to actions
	anytime action is triggered it returns a function
	which then triggers redux thunk, thunk passes dispatch to function 
*/
export const requestRobots = () => (dispatch) => {
	dispatch({ type: REQUEST_ROBOTS_PENDING })
	fetch('https://jsonplaceholder.typicode.com/users')
		.then((response) => response.json())
		.then((data) => dispatch({ type: REQUEST_ROBOTS_SUCCESS, payload: data }))
		.catch((error) => dispatch({ type: REQUEST_ROBOTS_FAILED, payload: error }))
}
